import React, {useEffect} from 'react';

function Sketch() {
    useEffect(() => {
        fetch("http://localhost:5000/api")
        .then(res => res.json())
        .then(data => {
            document.getElementById("test-msg").innerHTML=data.username;
        });
    })

    return (
        <div>
            <div id='test-msg' style={{color:'white'}}>

            </div>
        </div>
    );
}

export default Sketch;